<template>
  <div>
    <!-- top -->
    <div id="top">
      <div
        @click="btnALL"
        class="topbox"
      >
        <div>
          {{leftName=='全部酒店'?leftName:leftName+'栋'}}
          <img
            src="../../assets/two/icon_more.png"
            alt
          />
        </div>
      </div>
      <div
        @click="btnRent"
        class="topbox"
      >
        <div>
          {{center_1}}
          <img
            src="../../assets/two/icon_more.png"
            alt
          />
        </div>
      </div>
      <div
        @click="btnStatus"
        class="topbox"
      >
        <div>
          {{center_2}}
          <img
            src="../../assets/two/icon_more.png"
            alt
          />
        </div>
      </div>
    </div>
    <!-- room -->
    <div id="bottom">
      <div
        v-for="(item,index) in rooms"
        :key="index"
      >
        <div class="buildNum">
          <p>
            {{item.buildNum=='99'||!item.buildNum?'其他':item.buildNum+'栋'}}
            (共{{item.sum}}间)
          </p>
        </div>
        <div
          class="floor"
          v-for="(child,i) in item.children"
          :key="i"
        >
          <div>
            <p></p>
            <p>{{child.floor+'楼'}}</p>
            <p></p>
          </div>
          <div class="room_box">
            <div
              v-for="(room,i) in child.children"
              :key="i"
              class="bigbox"
            >
              <div
                class="roomdiv"
                :class="room.cooperId > 0 ?'c21':'c'+(room.status)+(room.isRoomCheck=='0'?'0':'1')"
                @click="btnInfo(room)"
              >
                <p class="num">
                  <label style="overflow:hidden;text-overflow:ellipsis;">{{ room.roomSn }}</label>
                  <img
                    v-if="room.endTime && toDay == room.endTime.substring(0,10)"
                    src="../../assets/two/icon_leaving.png"
                    alt
                  />
                </p>
                <div class="imgBox">
                  <div style="-border:1px solid green;">
                    <div v-if="room.status == 2 && hotelType == '4'">{{ room.bedIn || '0' }}/{{ room.bedNum || '0' }}</div>
                    <img
                      v-if="room.status == 3 && room.isRoomCheck != 0"
                      :src="require('../../assets/two/cleaning_night.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 3 && room.isRoomCheck == 0"
                      :src="require('../../assets/two/icon_check.png')"
                      alt
                    />
                    <img
                      v-if="room.show_dao"
                      :src="require('../../assets/two/icon_dao.png')"
                      alt
                    />
                  </div>
                  <div style="-border:1px solid red;">
                    <img
                      v-if="room.status == 1"
                      :src="require('../../assets/two/empty.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 2 && hotelType != '4'"
                      :src="require('../../assets/two/icon_people.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 4"
                      :src="require('../../assets/two/icon_locking.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 5"
                      :src="require('../../assets/two/lock.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 2 && room.roomType == 1"
                      :src="require('../../assets/two/night.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 2 && room.roomType == 2"
                      :src="require('../../assets/two/icon_clock.png')"
                      alt
                    />
                    <img
                      v-if="room.status == 2 && room.roomType == 3 || room.status == 2 && hotelType == '4'"
                      :src="require('../../assets/two/rent.png')"
                      alt
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- 弹出层-infoBox -->
    <mt-popup
      v-model="show_info"
      popup-transition="popup-fade"
      position="center"
      style="border-radius:5px;width:90%;text-align:left;font-size:0.7rem;color:#888;"
    >
      <div id="showbox">
        <p>
          <span>{{ infoBox.roomSn }}</span>
          <!-- <span>{{ infoBox.roomScale?infoBox.roomScale.scaleName:'' }}</span> -->
        </p>
        <!-- 公寓 -->
        <p v-if="bedInfoAll.length > 0">
          <span class="open_img">
            <img src="@/assets/two/bed.png" />
            <span style="padding-left:4px;">
              <span style="color:#02bc1fc4;">{{ infoBox.bedIn || '0' }}</span>
              <span>/{{ infoBox.bedNum || '0' }}</span>
            </span>
          </span>
          <span class="open_img">
            <img src="@/assets/two/person_blue.png" />
            <span
              style="padding-left:4px;"
              v-for="(item,i) in bedInfoAll"
              :key="i"
            >
              <span>{{ item.order.roomUser }}；</span>
            </span>
          </span>
        </p>
        <p class="bottomBox">
          <span v-if="liveInfo">
            <img
              v-if="infoBox.roomType==1"
              src="../../assets/two/night_orange.png"
              alt
            />
            <img
              v-if="infoBox.roomType==2"
              src="../../assets/two/clock_purple.png"
              alt
            />
            <img
              v-if="infoBox.roomType==3 && infoBox.cooperId <= 0"
              src="../../assets/two/night_green.png"
              alt
            />
            <span v-if="liveInfo.orderType==3">
              <span>{{liveInfo.order.startTime.substr(5,5)}} 至 {{liveInfo.order.endTime.substr(5,5)}}</span>
              <span>共{{((liveInfo.order.endTime.substr(0,4))-(liveInfo.order.startTime.substr(0,4)))*12+((liveInfo.order.endTime.substr(5,2)-liveInfo.order.startTime.substr(5,2)))}}月</span>
            </span>
            <span v-else>{{liveInfo.order.startTime.substr(5,5)}}至{{liveInfo.order.endTime.substr(5,5)}}&emsp;&emsp;共{{ liveInfo.order.diffDay }}{{liveInfo.orderType==2?'小时':'晚'}}</span>
            <span>房费:&yen;{{ liveInfo.orderType==3 || liveInfo.orderType==4 ?liveInfo.order.price:liveInfo.isCon == 0? (liveInfo.roomPrice-liveInfo.deposit).toFixed(2) :liveInfo.roomPrice }}</span>
          </span>
          <span
            v-if="infoBox.status == 3 && infoBox.isRoomCheck != 0"
            class="cleaning"
          >
            <img
              v-if="infoBox.roomType==1"
              src="../../assets/two/icon_clean.png"
              alt
            />
            <span>房间需要清洁，清洁完成后才能分房</span>
          </span>
          <span
            v-if="infoBox.status == 3 && infoBox.isRoomCheck == 0"
            class="cleaning"
          >
            <img
              v-if="infoBox.roomType==1"
              src="../../assets/two/icon_check_red.png"
              alt
            />
            <span>顾客正在等待您退押，请完成查房</span>
          </span>
          <label v-if="liveInfo">
            <img
              src="../../assets/two/person_blue.png"
              alt
            />
            <span v-if="liveInfo.order.orderSource=='1'">{{ liveInfo.sysUser != null?liveInfo.sysUser.realName:liveInfo.order.roomUser}}</span>
            <span v-else>{{ liveInfo.order.roomUser?liveInfo.order.roomUser:'未录入' }}</span>
          </label>
        </p>
        <div v-if="bedInfoAll.length > 0">
          <mt-button
            name="idMoreBox"
            class="boxBottom"
          >查看详情</mt-button>
        </div>
        <div v-else-if="ROLE_HOTEL_OWNER == true">
          <mt-button
            @click="historyIncome"
            class="boxBottom"
          >历史记录</mt-button>
        </div>
      </div>
    </mt-popup>
    <!-- left -->
    <mt-popup
      v-model="show_left"
      position="bottom"
      style="width:100%;"
    >
      <div
        class="type_box"
        @click="choosed_all('全部酒店')"
      >全部酒店</div>
      <div
        v-for="(item,i) in top_rooms.filter(item=>item.buildNum)"
        :key="i"
        @click="choosed_all(item.buildNum)"
        class="type_box"
      >
        <span>{{item.buildNum+'栋'}}</span>
      </div>
    </mt-popup>
    <!-- center_1 -->
    <mt-popup
      v-model="show_center"
      position="bottom"
      style="width:100%;"
    >
      <div
        class="type_box"
        @click="choosed_rent('租赁类型')"
      >租赁类型</div>
      <div
        v-for="(item,i) in rents"
        :key="i"
        @click="choosed_rent(item)"
        class="type_box"
      >
        <span>{{item.dictLabel}}</span>
      </div>
    </mt-popup>
    <!-- center_2 -->
    <mt-popup
      v-model="show_center_2"
      position="bottom"
      style="width:100%;"
    >
      <div
        class="type_box"
        @click="choosed_statu('房间状态')"
      >房间状态</div>
      <div
        v-for="(item,i) in roomStatus"
        :key="i"
        @click="choosed_statu(item)"
        class="type_box"
      >
        <span>{{item.dictLabel}}</span>
      </div>
    </mt-popup>
    <!-- 更多操作-liveInfo -->
    <mt-popup
      v-model="more_box"
      position="bottom"
      style="width:100%;"
    >
    </mt-popup>
  </div>
</template>
<script>
import dayjs from 'dayjs';
import { GetBusinessCooperOrderroomByroom } from '@/api/api_business';
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessOrderroomByroom, GetBusinessRoombyhotel, GetBusinessOrderroom } from '@/api/api_business_hotel_and_scale';
import { GetBusinessOrderroomAcByAccountHotel, GetBusinessOrderroomByroombed } from '@/api/api_agreement';
export default {
    data() {
        return {
            new_list: [],
            rooms: [],
            top_rooms: [],
            show_info: false,
            rents: [],
            roomStatus: [],
            sorts: [],
            bedInfoAll: [],
            newData: {
                hotelId: '0',
                landlord: this.$route.query.landlord
            },
            rent: '租赁类型',
            infoBox: '',
            liveInfo: '',
            currentRole: '',
            toDay: '',
            show_left: false,
            dome: [],
            domeName: '',
            leftName: '全部酒店',
            show_center: false,
            center_1: '租赁类型',
            show_center_2: false,
            center_2: '房间状态',
            show_right: false,
            a: '',
            more_box: false,
            more: '',
            password_show: false,
            ROLE_MANAGER: false,
            ROLE_HOTEL_OWNER: false,
            pass_phone: '',
            login_info: null,
            hotelType: null
        };
    },
    created() {
        this.$store.commit('setHeader', '实时房态');
        this.hotelId = this.$route.query.hotelId;
        let login_ok_info = JSON.parse(localStorage.getItem('current_user'));
        if (login_ok_info.id == '1') {
            this.ROLE_MANAGER = true;
        } else {
            this.roles = JSON.parse(localStorage.getItem('roles'));
            this.roles.some((item) => {
                if (item.name == 'ROLE_MANAGER') {
                    return (this.ROLE_MANAGER = true);
                } else {
                    return (this.ROLE_MANAGER = false);
                }
            });
            if (this.roles[0].name == 'ROLE_HOTEL_OWNER') {
                this.ROLE_HOTEL_OWNER = true;
            }
        }
        this.hotelType = localStorage.getItem('c_hotelType');
        this.param = {
            hotelId: this.hotelId
        };
        this.topList();
        this.getTop();
        let data = new Date();
        let m = data.getMonth() + 1 < 10 ? '0' + (data.getMonth() + 1) : data.getMonth() + 1;
        let d = data.getDate() < 10 ? '0' + data.getDate() : data.getDate();
        this.toDay = data.getFullYear() + '-' + m + '-' + d;
        let H = data.getHours() < 10 ? '0' + data.getHours() : data.getHours();
        let Mm = data.getMinutes() < 10 ? '0' + data.getMinutes() : data.getMinutes();
        let S = data.getSeconds() < 10 ? '0' + data.getSeconds() : data.getSeconds();
        this.a = this.toDay + ' ' + H + ':' + Mm + ':' + S;
    },
    computed: {
        computerWidth() {
            return (window.innerWidth - 40) / 3 + 'px';
        }
    },
    methods: {
        // top
        getTop() {
            GetSystemDictDataByType({ dictType: 'business_order_type' }).done((res) => {
                this.rents = res.data;
            });
            GetSystemDictDataByType({ dictType: 'business_room_status' }).done((res) => {
                this.roomStatus = res.data;
            });
        },
        // 酒店全部房间
        topList() {
            if (this.$route.query.accountUnitId) {
                let data = {
                    hotelId: this.$route.query.hotelId,
                    accountUnitId: this.$route.query.accountUnitId
                };
                GetBusinessOrderroomAcByAccountHotel(data).done((result) => {
                    let oldDataArray = result.data.sort(this.objectArraySort('roomSn')).reverse();
                    //循环数组，先buildNum，floor分组
                    var buildNumArray = [];
                    var floorKey = [];
                    //下标
                    var buildNumArrayIndex = 0;
                    var floorKeyIndex = 0;
                    for (var i = 0; i < oldDataArray.length; i++) {
                        if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                            buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                            buildNumArrayIndex++;
                        }
                        if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                            floorKey[floorKeyIndex] = oldDataArray[i].floor;
                            floorKeyIndex++;
                        }
                    }
                    var buildNumData = [];
                    for (var i = 0; i < buildNumArray.length; i++) {
                        var tempData = [];
                        var temoIndex = 0;
                        for (var j = 0; j < oldDataArray.length; j++) {
                            if (buildNumArray[i] == oldDataArray[j].buildNum) {
                                tempData[temoIndex] = oldDataArray[j];
                                temoIndex++;
                            }
                        }

                        var index = 0;
                        for (var h = 0; h < floorKey.length; h++) {
                            var floorChilren = [];
                            var floorChilrenIndex = 0;
                            for (var j = 0; j < tempData.length; j++) {
                                if (tempData[j].floor == floorKey[h]) {
                                    floorChilren[floorChilrenIndex] = tempData[j];
                                    floorChilrenIndex++;
                                }
                            }
                            //存在才添加子集，否则不添加，防止数组出现empty元素
                            if (floorChilren.length > 0) {
                                var floorObj = { floor: floorKey[h], children: floorChilren };
                                buildNumChilrenData[index] = floorObj;
                                index++;
                            }
                        }
                        let buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                        buildNumChilrenData.reverse();
                        if (buildNumChilrenData.length > 0) {
                            var buildNumObj = {
                                buildNum: buildNumArray[i],
                                sum: tempData.length,
                                children: buildNumChilrenData
                            };
                            buildNumData[i] = buildNumObj;
                        }
                    }
                    buildNumData.sort(this.objectArraySort('buildNum'));
                    this.rooms = buildNumData.reverse();
                    this.top_rooms = buildNumData;
                    this.top_rooms = buildNumData;
                });
            } else {
                let data = {
                    hotelId: '0',
                    landlord: this.$route.query.landlord
                };
                GetBusinessRoombyhotel(data).done((res) => {
                    let oldDataArray = res.data.sort(this.objectArraySort('roomSn')).reverse();
                    //循环数组，先buildNum，floor分组
                    var buildNumArray = [];
                    var floorKey = [];
                    //下标
                    var buildNumArrayIndex = 0;
                    var floorKeyIndex = 0;
                    for (var i = 0; i < oldDataArray.length; i++) {
                        this.isBeforeTime = dayjs(oldDataArray[i].endTime).isBefore(dayjs());
                        if ((oldDataArray[i].status == '2' && oldDataArray[i].roomType == '3' && this.isBeforeTime == true) || (oldDataArray[i].status == '2' && oldDataArray[i].roomType == '4' && this.isBeforeTime == true)) {
                            oldDataArray[i].show_dao = true;
                        } else {
                            oldDataArray[i].show_dao = false;
                        }
                        if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                            buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                            buildNumArrayIndex++;
                        }
                        if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                            floorKey[floorKeyIndex] = oldDataArray[i].floor;
                            floorKeyIndex++;
                        }
                    }
                    var buildNumData = [];
                    for (var i = 0; i < buildNumArray.length; i++) {
                        var tempData = [];
                        var temoIndex = 0;
                        for (var j = 0; j < oldDataArray.length; j++) {
                            if (buildNumArray[i] == oldDataArray[j].buildNum) {
                                tempData[temoIndex] = oldDataArray[j];
                                temoIndex++;
                            }
                        }
                        var buildNumChilrenData = [];
                        var index = 0;
                        for (var h = 0; h < floorKey.length; h++) {
                            var floorChilren = [];
                            var floorChilrenIndex = 0;
                            for (var j = 0; j < tempData.length; j++) {
                                if (tempData[j].floor == floorKey[h]) {
                                    floorChilren[floorChilrenIndex] = tempData[j];
                                    floorChilrenIndex++;
                                }
                            }
                            //存在才添加子集，否则不添加，防止数组出现empty元素
                            if (floorChilren.length > 0) {
                                var floorObj = { floor: floorKey[h], children: floorChilren };
                                buildNumChilrenData[index] = floorObj;
                                index++;
                            }
                        }
                        buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                        buildNumChilrenData.reverse();
                        if (buildNumChilrenData.length > 0) {
                            var buildNumObj = {
                                buildNum: buildNumArray[i],
                                sum: tempData.length,
                                children: buildNumChilrenData
                            };
                            buildNumData[i] = buildNumObj;
                        }
                    }
                    buildNumData = buildNumData.sort(this.objectArraySort('buildNum'));
                    this.rooms = buildNumData.reverse();
                    this.top_rooms = buildNumData;
                });
            }
        },
        // 查询酒店房间
        findList() {
            GetBusinessRoombyhotel().done((res) => {
                let oldDataArray = res.data.sort(this.objectArraySort('roomSn')).reverse();
                //循环数组，先buildNum，floor分组
                var buildNumArray = [];
                var floorKey = [];
                //下标
                var buildNumArrayIndex = 0;
                var floorKeyIndex = 0;
                for (var i = 0; i < oldDataArray.length; i++) {
                    if (buildNumArray.indexOf(oldDataArray[i].buildNum) == -1) {
                        buildNumArray[buildNumArrayIndex] = oldDataArray[i].buildNum;
                        buildNumArrayIndex++;
                    }
                    if (floorKey.indexOf(oldDataArray[i].floor) == -1) {
                        floorKey[floorKeyIndex] = oldDataArray[i].floor;
                        floorKeyIndex++;
                    }
                }
                var buildNumData = [];
                for (var i = 0; i < buildNumArray.length; i++) {
                    var tempData = [];
                    var temoIndex = 0;
                    for (var j = 0; j < oldDataArray.length; j++) {
                        if (buildNumArray[i] == oldDataArray[j].buildNum) {
                            tempData[temoIndex] = oldDataArray[j];
                            temoIndex++;
                        }
                    }
                    var buildNumChilrenData = [];
                    var index = 0;
                    for (var h = 0; h < floorKey.length; h++) {
                        var floorChilren = [];
                        var floorChilrenIndex = 0;
                        for (var j = 0; j < tempData.length; j++) {
                            if (tempData[j].floor == floorKey[h]) {
                                floorChilren[floorChilrenIndex] = tempData[j];
                                floorChilrenIndex++;
                            }
                        }
                        //存在才添加子集，否则不添加，防止数组出现empty元素
                        if (floorChilren.length > 0) {
                            var floorObj = { floor: floorKey[h], children: floorChilren };
                            buildNumChilrenData[index] = floorObj;
                            index++;
                        }
                    }
                    buildNumChilrenData = buildNumChilrenData.sort(this.objectArraySort('floor'));
                    buildNumChilrenData.reverse();
                    if (buildNumChilrenData.length > 0) {
                        var buildNumObj = {
                            buildNum: buildNumArray[i],
                            sum: tempData.length,
                            children: buildNumChilrenData
                        };
                        buildNumData[i] = buildNumObj;
                    }
                }
                buildNumData = buildNumData.sort(this.objectArraySort('buildNum'));
                this.rooms = buildNumData.reverse();
            });
        },
        // 排序
        objectArraySort(keyName) {
            return function (objectN, objectM) {
                var valueN = objectN[keyName];
                var valueM = objectM[keyName];
                if (valueN < valueM) return 1;
                else if (valueN > valueM) return -1;
                else return 0;
            };
        },
        // 弹出框
        btnInfo(room) {
            this.infoBox = room;
            if (this.hotelType == '4') {
                this.getBedInfo(room.id);
            } else {
                if (room.cooperId > 0 || room.cleanCooper == 1) {
                    this.getCooperInfo(room.id);
                } else {
                    this.liveInfo = '';
                    if (this.infoBox.status == 2 || this.infoBox.status == 3) {
                        this.getInfo();
                    }
                }
            }
            this.show_info = true;
        },
        // 展开信息
        getBedInfo(roomId) {
            GetBusinessOrderroomByroombed({ roomId: roomId }).done((res) => {
                this.bedInfoAll = res.data;
            });
        },
        // 展开信息
        getCooperInfo(id) {
            GetBusinessCooperOrderroomByroom({ roomId: id }).done((res) => {
                this.liveInfo = res.data;
            });
        },
        // 展开信息
        getInfo() {
            GetBusinessOrderroomByroom(this.infoBox.id).done((res) => {
                let start_time = new Date(res.data.order.startTime);
                var current_time = new Date();
                current_time.setTime(start_time.getTime() + 24 * 60 * 60 * 1000);
                var current_y = current_time.getFullYear() < 10 ? '0' + current_time.getFullYear() : current_time.getFullYear();
                var current_m = current_time.getMonth() + 1 < 10 ? '0' + (current_time.getMonth() + 1) : current_time.getMonth() + 1;
                var current_d = current_time.getDate() < 10 ? '0' + current_time.getDate() : current_time.getDate();
                res.data.next_day = current_y + '-' + current_m + '-' + current_d + ' ' + '08:00:00';
                this.liveInfo = res.data;
            });
        },
        // 更多操作-暂无
        getMoreList() {
            this.show_info = false;
            if (this.liveInfo.id) {
                GetBusinessOrderroom(this.liveInfo.id).done((res) => {
                    this.more = res.data;
                });
            }
            this.more_box = true;
        },
        // 选中全部
        btnALL() {
            this.show_left = true;
        },
        // 选中租赁类型
        btnRent() {
            this.show_center = true;
        },
        // 选中房间状态
        btnStatus() {
            this.show_center_2 = true;
        },
        // 选中(栋)
        choosed_all(buildNum) {
            this.leftName = buildNum;
            if (buildNum != '全部酒店') {
                this.newData.buildNum = buildNum;
            } else {
                delete this.newData.buildNum;
            }
            this.show_left = false;
            this.findList();
        },
        // 选中(租赁)
        choosed_rent(item) {
            if (item != '租赁类型') {
                this.center_1 = item.dictLabel;
                this.newData.roomType = item.dictValue;
            } else {
                this.center_1 = item;
                delete this.newData.roomType;
            }
            this.show_center = false;
            this.findList();
        },
        // 选中(状态)
        choosed_statu(item) {
            if (item != '房间状态') {
                this.center_2 = item.dictLabel;
                this.newData.status = item.dictValue;
            } else {
                this.center_2 = item;
                delete this.newData.status;
            }
            this.show_center_2 = false;
            this.findList();
        },
        // 业主-历史记录
        historyIncome() {
            this.$router.push({
                path: '/owner-order',
                query: {
                    roomId: this.infoBox.id
                }
            });
        }
    }
};
</script>
